<template>
	<view>
		<button @click="openMessage">订阅消息</button>
	</view>
</template>

<script>
	export default {
		data() {
			return {

			}
		},
		onLoad() {
			const options = {
				title: '开启消息通知',
				content: '开启后可及时收到重要通知，是否前往设置开启？',
				confirmText: '去设置',
				cancelText: '暂不开启',
				successToast: '通知权限已开启，感谢支持！',
				failToast: '您未开启通知权限，可能错过重要消息',
				successCallback: () => {},
				failCallback: () => {}
			}

			// 显示引导弹窗
			uni.showModal({
				title: options.title,
				content: options.content,
				confirmText: options.confirmText,
				cancelText: options.cancelText,
				success: (res) => {
					if (res.confirm) {
						uni.openSetting({
							success: (settingRes) => {
								if (uni.getSystemInfoSync().platform === 'wechat') {
									if (settingRes.authSetting['scope.subscribeMessage']) {
										uni.showToast({
											title: options.successToast,
											icon: 'success'
										})
										options.successCallback()
									} else {
										uni.showToast({
											title: options.failToast,
											icon: 'none'
										})
										options.failCallback()
									}
								}
							},
							fail: (err) => {
								console.error('打开设置失败', err)
								options.failCallback()
							}
						})
					} else {
						uni.showToast({
							title: '您可以随时在设置中开启通知',
							icon: 'none'
						})
						options.failCallback()
					}
				},
				fail: (err) => {
					console.error('显示弹窗失败', err)
					options.failCallback()
				}
			})
		},
		methods: {
			openMessage() {
				uni.requestSubscribeMessage({
					tmplIds: ['PN8Vc4Z5rWUHi05A6F-J73TkkpF4iHxkEtA6bIoFUPw'],
					success: (res) => {
						console.log(res)
					},
					fail: (err) => {
						console.log(err)
					},
				})

			}

		}
	}
</script>